import { Injectable } from '@angular/core';
import { HttpClient } from "@angular/common/http";
import { environment } from 'src/environments/environment';
import { WalkService } from '../models';

@Injectable({
	providedIn: 'root'
})
export class WalksService {
	private url = "walks";

	constructor(public http:HttpClient) {}

    public insert(race:WalkService){
		return this.http.post<any>(environment.backendURL + this.url, race, environment.httpOptions)
	}

    public list(sid:number){
        return this.http.get<WalkService[]>(environment.backendURL + this.url + '/byserviceid/' + sid, environment.httpOptions);
    }

	public get(id:number){
		return this.http.get<WalkService>(environment.backendURL + this.url + '/' + id, environment.httpOptions);
	}
}
