import { Component, Input, OnInit } from '@angular/core';

@Component({
  selector: 'app-walk-abstract',
  templateUrl: './walk-abstract.component.html',
  styleUrls: ['./walk-abstract.component.css']
})
export class WalkAbstractComponent implements OnInit {
  @Input('show-info-func')
  showInfo: () => void

  @Input('show-payment-methods-func')
  showPaymentMethods:(total:number) => void

  @Input('city')
  city:any

  @Input('dogs')
  dogs:any[]

  @Input('dates')
  dates:any[]


  constructor() { }

  ngOnInit(): void {
  }

  getTotal() {
    let sum: number = 0;

    if (this.city) {
      this.dates.forEach(dat => {
        sum += Number(this.city.prices.find(p => p.type == 'WK' && p.hours == dat.hours).value);
      })

      return this.dogs.length * sum;
    }
    return 0;
  }

  getHoursPrice(hours) {
    return this.city.prices.find(p => p.hours == hours).value;
  }
}
