import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { HomeComponent } from './pages/home/home.component';
import { PricingComponent } from './pages/pricing/pricing.component';
import { AboutUsComponent } from './pages/about-us/about-us.component';

import { LoginComponent } from './pages/auth/login/login.component';
import { RegistrationComponent } from './pages/auth/registration/registration.component';

import { VerifyEmailComponent } from './pages/verify-email/verify-email.component';
import { ProfileComponent } from './pages/profile/profile.component';
import { DogProfileComponent } from './pages/dashboard/pets/dog-profile/dog-profile.component';
import { DashboardComponent } from './pages/dashboard/dashboard.component';
import { PaymentComponent } from './pages/payment/payment.component';

import { StartComponent } from './pages/dashboard/start/start.component';
import { ActiveServicesComponent } from './pages/dashboard/services/active-services/active-services.component';
import { ServiceComponent } from './pages/dashboard/services/service/service.component';
import { AddPetComponent } from './pages/dashboard/pets/add-pet/add-pet.component';
import { ListPetsComponent } from './pages/dashboard/pets/list-pets/list-pets.component';
import { ClientHistoryComponent } from './pages/dashboard/services/client-history/client-history.component';
import { WalkerHistoryComponent } from './pages/dashboard/services/walker/walker-history/walker-history.component';
import { WalkerServiceComponent } from './pages/dashboard/services/walker/walker-service/walker-service.component';
import { ManageCitiesComponent } from './pages/dashboard/cities/manage-cities/manage-cities.component';
import { ManageServicesComponent } from './pages/dashboard/services/manage-services/manage-services.component';
import { ManageWalkersComponent } from './pages/dashboard/walker/manage-walkers/manage-walkers.component';
import { TrackingComponent } from './pages/dashboard/services/tracking/tracking.component';
import { BeWalkerComponent } from './pages/dashboard/walker/be-walker/be-walker.component';
import { RequestWalkerComponent } from './pages/request-walker/request-walker.component';
import { WaitedServicesComponent } from './pages/dashboard/services/waited-services/waited-services.component';
import { ServicesComponent } from './pages/dashboard/services/services.component';
import { PetsComponent } from './pages/dashboard/pets/pets.component';
import { ServiceDetailsComponent } from './pages/dashboard/services/service-details/service-details.component';
import { PaidServicesComponent } from './pages/dashboard/services/manage-services/paid/payment.component';
import { ApprovedServicesComponent } from './pages/dashboard/services/manage-services/approved/approved.component';
import { InProgressServicesComponent } from './pages/dashboard/services/manage-services/in-progress/in-progress.component';
import { FinishedServicesComponent } from './pages/dashboard/services/manage-services/finished/finished.component';
import { ServiceProgressComponent } from './pages/dashboard/services/service-progress/service-progress.component';
import { UserDetailsComponent } from './pages/dashboard/users/user-details/user-details.component';
import { UsersListComponent } from './pages/dashboard/users/users-list/users-list.component';

const routes: Routes = [
	{path:'', component:HomeComponent},
	{path:'pricing', component:PricingComponent},
	{path:'about-us', component:AboutUsComponent},
	{path:'login', component:LoginComponent},
	{path:'registration', component:RegistrationComponent},
	{path: 'request-walker', component:RequestWalkerComponent },
	{
		path:'dashboard', 
		component:DashboardComponent,
		children:[
			{ path: '', component: StartComponent },
			{
				path:'services', 
				component:ServicesComponent,
				children: [
					{ path: 'request', component: ServiceComponent },
					{ path: 'details/:ID', component: ServiceDetailsComponent },
					{ path: 'active', component: ActiveServicesComponent },
					{ path: 'waited', component: WaitedServicesComponent },
					{ path: 'history', component: ClientHistoryComponent },
					{ path: 'scheduled', component: WalkerServiceComponent },
					{ path: 'walker/history', component: WalkerHistoryComponent },
					{ path: 'progress/:ID', component: ServiceProgressComponent },
					{ path: 'tracking', component: TrackingComponent },
					{ 
						path: 'manage', 
						component: ManageServicesComponent,
						children:[
							{ path: 'paid', component:PaidServicesComponent },
							{ path: 'approved', component:ApprovedServicesComponent },
							{ path: 'in-progress', component:InProgressServicesComponent },
							{ path: 'finished', component:FinishedServicesComponent }
						]
					},
				]
			},
			{
				path: 'pets',
				component: PetsComponent,
				children: [
					{path: '', component: ListPetsComponent},
					{path: 'add', component: AddPetComponent},
					{path:'details', component:DogProfileComponent},
				]
			},
			{path: 'cities/manage', component: ManageCitiesComponent},
			{path: 'walkers/manage', component: ManageWalkersComponent},
			{path: 'walkers/be', component: BeWalkerComponent},

			{ path: 'users/:ID/details/:TYPE', component: UserDetailsComponent },
			{ path: 'users', component: UsersListComponent }

		]
	},
	{path:'payment', component:PaymentComponent},
	{path:'verify-email', component:VerifyEmailComponent},
	{path:'profile', component:ProfileComponent},
	//{path:'test', component:TestApiComponent},
    { path: '**', redirectTo: 'not-found' }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { relativeLinkResolution: 'legacy' })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
