import { Component, Input, OnInit } from '@angular/core';
import { WalkDate } from 'src/app/models';
import { getFormattedCurrentDate } from 'src/app/models/utilities';

@Component({
  selector: 'app-walk-form',
  templateUrl: './walk-form.component.html',
  styleUrls: ['./walk-form.component.css']
})
export class WalkFormComponent implements OnInit {
  @Input('show-abstract-func')
  showAbstract: (isFailed:boolean, dates:WalkDate[]) => void

  @Input('is-showing')
  isShowing:boolean

  @Input('dates')
  dates: WalkDate[];

  getFormattedDate:(days?:number) => string = getFormattedCurrentDate

  constructor() {}

  ngOnInit(): void {
  }

  addDate() {
    if(!this.dates)
      this.dates = [];

    this.dates.push({ datetime: null, hours: 1 });
  }

  removeDate(i) {
    this.dates.splice(i, 1);
  }

  constructDates(form:HTMLFormElement){
    let rows = form.getElementsByClassName('wtd-date');
    let inputs:HTMLCollectionOf<HTMLInputElement>;
    let row:HTMLElement;

    let isFailed = false
    let dates:WalkDate[] = []

    for (let i = 0; i < rows.length; i++) {
      row = rows.item(i) as HTMLElement
      inputs = row.getElementsByClassName('form-datetime') as HTMLCollectionOf<HTMLInputElement>
      if(inputs.item(0).value == "" || inputs.item(0).value == "")
        isFailed = true
      else
        dates.push({ 
          date:inputs.item(0).value, 
          time:inputs.item(1).value, 
          hours:Number(inputs.item(2).value),
          datetime: new Date(inputs.item(0).value + ' ' + inputs.item(1).value)
        })
    }

    this.showAbstract(isFailed, dates)
  }
}
