import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { SwPush } from '@angular/service-worker';
import { environment } from 'src/environments/environment';
import { PushData } from '../models';

@Injectable({
  providedIn: 'root'
})
export class NotificationsService {

  constructor(
    private swPush: SwPush,
    private http: HttpClient,

  ) { }

  subscribeNotifications() {
    this.swPush.requestSubscription({
      serverPublicKey: environment.VAPIIDPublicKey
    }).then(pushSub => {
      let st = JSON.stringify(pushSub)
      let sub: PushData = JSON.parse(st)
      sub.ownerId = Number(localStorage.getItem('id'))
      if (!localStorage.getItem("notifySub") || localStorage.getItem("notifySub") != st) {
        this.saveDevice(sub).subscribe(res => {
          if (res.ok)
            localStorage.setItem("notifySub", st)
        })
      }

    }).catch(err => { })
  }

  saveDevice(sub: PushData) {
    return this.http.post<any>(environment.backendURL + 'devices', sub, environment.httpOptions)
  }

  notifyPayment(payment) {
    return this.http.post(environment.backendURL + 'services/payment', payment, environment.httpOptions)
  }
}
