import { Component, OnInit } from '@angular/core';
import { ServiceRequest } from 'src/app/models';
import { getFormattedDate } from 'src/app/models/utilities';
import { ServicesService } from 'src/app/services/services.service';

interface Res {
	ok?:boolean
}

@Component({
	selector: 'app-client-history',
	templateUrl: './client-history.component.html',
	styleUrls: ['./client-history.component.css']
})
export class ClientHistoryComponent implements OnInit {
	services:ServiceRequest[]
	
	constructor(private servicesS:ServicesService) { }

	ngOnInit(): void {
		let id = Number(localStorage.getItem('id'));
		this.servicesS.getFinishedByClientId(id).subscribe(services => this.services = services)
	}

	listDates(s:ServiceRequest){
		return s.dates
		.sort((a, b) => new Date(a.date).getTime() - new Date(b.date).getTime() )
		.map(d => getFormattedDate(new Date(d.date + 'T05:00:00')))
		.join(", ")
	}
}
