import { Component, OnInit } from '@angular/core';
import { ServiceRequest } from 'src/app/models';
import { ServicesService } from 'src/app/services/services.service';

@Component({
  selector: 'app-finished',
  templateUrl: './finished.component.html',
  styleUrls: ['./finished.component.css']
})
export class FinishedServicesComponent implements OnInit {
  services:ServiceRequest[];
  servicesFilter:ServiceRequest[];

  constructor(private servicesS:ServicesService) {
    this.servicesS.getFinished().subscribe(services => this.services = this.servicesFilter = services)
  }

  ngOnInit(): void {
  }

  searchServices(e){
		let s = e.target.value.toUpperCase();
		
		this.servicesFilter = this.services.filter(u => ("WTD" + u.id).includes(s));
	}

}
