import { Component, OnInit } from '@angular/core';
import { ServicesService } from 'src/app/services/services.service';
import { ServiceRequest } from 'src/app/models';
import { getFormattedDate } from 'src/app/models/utilities';

@Component({
	selector: 'app-walker-service',
	templateUrl: './walker-service.component.html',
	styleUrls: ['./walker-service.component.css']
})
export class WalkerServiceComponent implements OnInit {
	services:ServiceRequest[];

	constructor(private servicesS:ServicesService) {
		this.servicesS.getInProgressByWalkerId(Number(localStorage.getItem('id')))
		.subscribe(services => this.services = services);
	}
	
	ngOnInit(): void {	
	}

	isToday(dd){
		const today = new Date()
		return dd.getDate() == today.getDate() 
			&& dd.getMonth() == today.getMonth() 
			&& dd.getFullYear() == today.getFullYear()
	}

	listDates(s:ServiceRequest){
		return s.dates
		.sort((a, b) => new Date(a.date).getTime() - new Date(b.date).getTime() )
		.map(d => getFormattedDate(new Date(d.date + 'T05:00:00')))
		.join(", ")
	}
}
