import { Component, OnInit } from '@angular/core';
import { ServiceRequest } from 'src/app/models';
import { AlertsService } from 'src/app/services/alerts.service';
import { ServicesService } from 'src/app/services/services.service';

@Component({
  selector: 'app-in-progress',
  templateUrl: './in-progress.component.html',
  styleUrls: ['./in-progress.component.css']
})
export class InProgressServicesComponent implements OnInit {
  services:ServiceRequest[];
  servicesFilter:ServiceRequest[];

  constructor(private servicesS:ServicesService, private alert:AlertsService) {
    this.servicesS.getInProgress().subscribe(services => this.services = this.servicesFilter = services)
  }

  ngOnInit(): void {
  }

  searchServices(e){
		let s = e.target.value.toUpperCase();
		
		this.servicesFilter = this.services.filter(u => ("WTD" + u.id).includes(s));
	}

}
